<template>
  <div>
    <TopMenu :isLogin="true" :isMyHouse="true" />
    <el-card
      class="box-card"
      style="margin-top: 20px; margin-right: 15%; margin-left: 15%"
    >
      <div style="display: inline-block">
        <el-avatar
          :size="80"
          src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"
        ></el-avatar>
        <el-upload
          style="margin-left: 5px"
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
          :auto-upload="false"
        >
          <el-button type="text"></el-button>
        </el-upload>
      </div>
      <div
        style="display: inline-block; vertical-align: top; margin-left: 30px"
      >
        <div style="margin-top: 10px"><span></span>123456</div>
        <div style="margin-top: 10px"><span></span>2020/09/09</div>
      </div>
    </el-card>
    <el-table :data="tableData" style="width: 70%; margin: 20px auto">
      <el-table-column prop="col1">
        <template slot-scope="scope">
          <strong>{{ scope.row.col1 }}</strong>
        </template>
      </el-table-column>
      <el-table-column prop="col2">
        <template slot-scope="scope">
          <i
            v-if="scope.row.col2"
            style="color: green; font-size: 20px"
            class="el-icon-success"
          ></i>
          <i
            v-else
            style="color: red; font-size: 20px"
            class="el-icon-question"
          ></i>
        </template>
      </el-table-column>
      <el-table-column prop="col3" width="300px"> </el-table-column>
      <el-table-column prop="col4">
        <template slot-scope="scope">
          <template v-if="scope.row.edit">
            <el-select v-if="scope.row.col1 == ''" v-model="scope.row.col4">
              <el-option key="" label="" value=""> </el-option>
              <el-option key="" label="" value=""> </el-option>
            </el-select>
            <el-input
              v-else-if="scope.row.col1 == ''"
              show-password
              v-model="scope.row.col4"
            >
            </el-input>
            <el-dialog
              v-else-if="scope.row.col1 == ''"
              title=""
              :visible.sync="scope.row.edit"
              width="60%"
            >
              <el-form
                :model="identityForm"
                :rules="rules"
                ref="IDCardForm"
                label-width="80px"
                style="width: 80%"
              >
                <el-form-item label="" prop="name">
                  <el-input
                    v-model="identityForm.name"
                    placeholder=""
                  ></el-input>
                </el-form-item>
                <el-form-item label="" prop="type" required>
                  <el-select v-model="identityForm.type" style="width: 250px">
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="code">
                  <el-input
                    v-model="identityForm.code"
                    placeholder=""
                  ></el-input>
                </el-form-item>
                <el-form-item label="" prop="uploadIDCard">
                  <el-upload
                    :on-change="handleChange"
                    :on-remove="handleRemove"
                    ref="upload"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :file-list="fileList"
                    :auto-upload="false"
                    :limit="1"
                  >
                    <el-button slot="trigger" size="small" type="primary">
                      
                    </el-button>
                  </el-upload>
                </el-form-item>
              </el-form>
              <span slot="footer" class="dialog-footer">
                <el-button @click="scope.row.edit = false"></el-button>
                <el-button
                  type="primary"
                  @click="submitUploadIDCard(scope.row)"
                >
                  
                </el-button>
              </span>
            </el-dialog>
            <el-input v-else v-model="scope.row.col4"> </el-input>
          </template>
          <template v-else>
            <el-input
              class="setting-password"
              v-if="scope.row.col1 == ''"
              show-password
              readonly="readonly"
              :value="scope.row.col4"
            >
            </el-input>
            <div v-else>{{ scope.row.col4 }}</div>
          </template>
        </template>
      </el-table-column>
      <el-table-column prop="col5">
        <template slot-scope="scope">
          <el-button
            v-if="!scope.row.edit"
            type="primary"
            size="mini"
            @click="handleEdit(scope.row)"
          >
            
          </el-button>
          <el-button
            v-else
            type="danger"
            size="mini"
            @click="handleSave(scope.row)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
import TopMenu from "../components/TopMenu.vue";

export default {
  data() {
    var validateFile = (rule, value, callback) => {
      console.log(this.fileList.length);
      if (this.fileList.length == 0) {
        return callback(new Error(""));
      }
    };
    return {
      tableData: [
        {
          col1: "",
          col2: false,
          col3: "",
          col4: "",
          col5: "edit",
          edit: false,
        },
        {
          col1: "",
          col2: true,
          col3: "",
          col4: "",
          col5: "edit",
          edit: false,
        },
        {
          col1: "",
          col2: true,
          col3: "6",
          col4: "******",
          col5: "edit",
          edit: false,
        },
        {
          col1: "",
          col2: true,
          col3: "",
          col4: "2536456457467",
          col5: "edit",
          edit: false,
        },
        {
          col1: "",
          col2: true,
          col3: "",
          col4: "123456@qq.com",
          col5: "edit",
          edit: false,
        },
      ],
      identityForm: {
        name: "",
        type: "",
        code: "",
      },
      fileList: [],
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        code: [{ required: true, message: "", trigger: "blur" }],
        uploadIDCard: [
          { required: true, validator: validateFile, trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    handleAvatarSuccess() {},
    beforeAvatarUpload() {},
    handleEdit(row) {
      row.edit = true;
    },
    handleSave(row) {
      // axios.post
      row.edit = false;
      if (row.col4 != "") {
        row.col2 = true;
      }
    },
    submitUploadIDCard(row) {
      this.$refs["IDCardForm"].validate((valid) => {
        if (valid) {
          row.edit = false;
          // this.$refs.upload.submit();
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    handleChange(file, fileList) {
      this.fileList = fileList;
    },
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },
  },
  components: {
    TopMenu,
  },
};
</script>

<style>
.setting-password .el-input__inner {
  border: none !important;
  background: rgba(255, 255, 255, 0);
  padding: 0;
}
</style>